import React, { useRef, createRef } from 'react'
import styled from 'styled-components'

import { Header, Introduction, OtherExperience, Skill, WorkExperience, Project } from '../components/resume'

const Resume = (props) => {
  const navList = ['Work Experience', 'Project', 'Skill', 'Other Experience']

  const sectionRefs = useRef(navList.map(() => createRef()))

  return (
    <>
      <StyledContainer>
        <Introduction />
        <StyledRowRight>
          <Header sectionRefs={sectionRefs} navList={navList} />
          <WorkExperience ref={sectionRefs.current[0]} />
          <Project ref={sectionRefs.current[1]} />
          <Skill ref={sectionRefs.current[2]} />
          <OtherExperience ref={sectionRefs.current[3]} />
        </StyledRowRight>
      </StyledContainer>
    </>
  )
}

const StyledContainer = styled.div`
  max-width: 75rem; // 이력서용
  padding: 0 1rem;
  margin: auto;
  @media only screen and (min-width: 768px) {
    max-width: 65rem;
  }
  @media only screen and (min-width: 1200px) {
    max-width: 75rem;
    width: 100%;

    display: flex;
  }
`

const StyledRowRight = styled.div`
  position: relative;
  max-width: 75rem; // 이력서용
  margin: auto;
  height: calc(100% - 3rem);
  padding: 0 0 3rem;
  display: flex;
  flex-direction: column;
  @media only screen and (min-width: 768px) {
    max-width: 65rem;
  }
  @media only screen and (min-width: 1200px) {
    width: calc(100% - 23rem);
    flex-grow: 1;
    -webkit-box-flex: 1;
    padding: 0 0 3rem;
  }
`

export default Resume
