import React from 'react'
import styled from 'styled-components'

const Header = ({ sectionRefs, navList }) => {
  return (
    <>
      <StyledNavContainer>
        <StyledUL>
          {navList.map((navItem, index) => {
            return (
              <StyledList
                key={`nav-item-${index}`}
                onClick={() => {
                  sectionRefs.current[index].current.scrollIntoView({ behavior: 'smooth' })
                }}
              >
                {navItem}
              </StyledList>
            )
          })}
        </StyledUL>
      </StyledNavContainer>
    </>
  )
}

const StyledNavContainer = styled.nav`
  display: none;
  position: fixed;
  max-width: 50rem;
  /* position: sticky; */
  top: 0;
  width: 100%;
  height: 3rem;
  background: #fff;
  opacity: 0.9;
  z-index: 10;
  @media only screen and (min-width: 768px) {
    display: unset;
    max-width: 63rem;
  }
  @media only screen and (min-width: 1200px) {
    display: unset;
    max-width: 50rem;
  }
`

const StyledUL = styled.ul`
  height: 100%;
  display: flex;
  align-items: center;
  column-gap: 1rem;
`

const StyledList = styled.li`
  padding: 0 0.3rem;
  /* font-weight: 600; */
  cursor: pointer;
  transition: color 0.2s ease-in-out;
  &::before {
    content: none;
  }
  &:hover {
    color: ${({ theme }) => theme.colors.primary};
  }
  font-size: 16px;
`

export default Header
