import React from 'react'
import styled from 'styled-components'

const Text = ({ children, type, margin, textAlign }) => {
  const styles = {
    margin: margin,
    textAlign: textAlign,
  }

  if (type === 'small') {
    return <StyledTextSmall {...styles}>{children}</StyledTextSmall>
  }

  if (type === 'base') {
    return <StyledTextBase {...styles}>{children}</StyledTextBase>
  }

  if (type === 'intro-main') {
    return <StyledIntroMainTitle {...styles}>{children}</StyledIntroMainTitle>
  }

  if (type === 'section-title') {
    return <StyledSectionTitle {...styles}>{children}</StyledSectionTitle>
  }

  return <StyledTitle {...styles}>{children}</StyledTitle>
}

Text.defaultProps = {
  margin: 'false',
  textAlign: 'false',
}

const StyledTextSmall = styled.p`
  font-size: ${({ theme }) => theme.fontSizes.small};
`

const StyledTextBase = styled.p`
  font-size: ${({ theme }) => theme.fontSizes.base};
`

const StyledIntroMainTitle = styled.h1`
  font-weight: 700;
  font-size: ${({ theme }) => theme.fontSizes.lg};
  ${(props) => (props.margin ? `margin: ${props.margin};` : '')};
  ${(props) => (props.textAlign ? `text-align: ${props.textAlign};` : '')};
`
const StyledSectionTitle = styled.h1`
  box-shadow: inset 0 -8px 0 ${({ theme }) => theme.colors.primary}; // 이력서용
  /* box-shadow: inset 0 -12px 0 ${({ theme }) => theme.colors.primary}; */

  display: inline-block;
  font-weight: 700;
  font-size: ${({ theme }) => theme.fontSizes.xl};

  line-height: 1.1;
  margin: 0 0 1rem;
  ${(props) => (props.textAlign ? `text-align: ${props.textAlign};` : '')};
  @media only screen and (min-width: 1200px) {
    margin: 0 0 2rem;
    box-shadow: inset 0 -14px 0 ${({ theme }) => theme.colors.primary};
  }
`

const StyledTitle = styled.h1`
  font-weight: 700;
  font-size: ${({ theme }) => theme.fontSizes.lg};
  margin: 0 0 0.5rem;
  ${(props) => (props.textAlign ? `text-align: ${props.textAlign};` : '')};
`

export default Text
